@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);

:root {
    --mud: #591902;
    --red-dull: #d93f07;
    --red-bright: #f23005;
    --gray: #d9d9d9;
    --black: #0d0d0d;
    --green: #0df205;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: #0d0d0d;
    background-color: var(--black);
}

::-webkit-scrollbar-thumb {
    width: 10px;
    background-color: #0d0d0d;
    background-color: var(--black);
}

::-webkit-scrollbar-track {
    background-color: #d93f07;
    background-color: var(--red-dull);
}
.card {
    display: flex;
    flex-direction: column;
    width: 30%;
    justify-content: space-between;
    background-color: var(--gray);
    align-items: center;
    text-align: center;
    margin: 1rem 0;
    height: 40vh;
    border-radius: 10px;
    text-decoration: none;
    color: var(--black);
}

.card-header {
    width: 100%;
    padding: 1rem;
}

.card-img {
    width: 100px;
    height: 100px;
    border-radius: 999px;
    object-fit: cover;
    object-position: center;
}

.card-body {
    width: 100%;
    text-align: center;
    text-transform: capitalize;
    background-color: var(--red-bright);
    padding: 1rem 0;
    border-radius: 10px;
    box-shadow: 0 5px 15px var(--red-dull);
}

.card-name {
    font-family: "Source Sans Pro", sans-serif;
    font-size: 1.5rem;
}

.button {
    padding: 1rem;
    border-radius: 10px;
    text-transform: uppercase;
    font-size: 1rem;
    font-family: "Source Sans Pro", sans-serif;
    outline: none;
    border: none;
    font-weight: bolder;
    width: 40%;
}

.button-shortlist {
    background-color: var(--green);
}

.button-reject {
    background-color: var(--red-bright);
}
.home-section {
    height: 100vh;
    width: 100vw;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
}

.navigation-block {
    display: flex;
    padding: 1rem;
    justify-content: center;
}

.nav-links {
    width: 80%;
    display: flex;
    list-style: none;
    align-items: center;
    background-color: var(--red-dull);
    border-radius: 10px;
}

.nav-item {
    width: 33.3333%;
    text-align: center;
    border-right: 1px solid var(--gray);
    display: flex;
}

.nav-item:last-child {
    border: none;
}

.nav-link {
    text-decoration: none;
    color: var(--black);
    font-family: "Lato", sans-serif;
    font-size: 1rem;
    width: 100%;
    height: 100%;
    padding: 1rem;
}

.active {
    background-color: var(--red-bright);
    box-shadow: 0 5px 15px var(--red-dull);
    color: var(--gray);
}

.not-found {
    font-family: "Source Sans Pro", sans-serif;
    font-size: 20rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--red-bright);
    flex-direction: column;
}

.not-found span {
    font-family: "Lato", sans-serif;
    font-size: 2rem;
    text-align: center;
    letter-spacing: 20px;
}

.candidates-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
}

.profile-section {
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
}

.profile-header {
    width: 40%;
}

.profile-header .profile-image {
    width: 100%;
}

.profile-body {
    width: 50%;
}

.profile-body .profile-name {
    font-size: 7rem;
    font-family: "Lato", sans-serif;
    color: var(--gray);
}

.profile-body .profile-details {
    font-size: 1.5rem;
    text-align: justify;
    font-family: "Source Sans Pro", sans-serif;
    color: var(--red-bright);
}

.button-section {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 2rem 0;
}

